@import url(https://fonts.googleapis.com/css?family=Corben:50,100,200,300,400,500,600,700);
.navbar {
  padding-top: 40px;
  background: transparent;
  height: 40px;
  display: flex;
  justify-content: space-between;;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  /* top: 10px; */
  width: 100%;
  z-index: 999;
  transition: .5s;
}


.navbar-up {
  padding: 1vh 5vw;
  width: 100%;
  background-color: #fff;
  /* background: linear-gradient(rgb(250, 249, 249) 0%, rgb(214, 214, 214) 100%); */
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  position: fixed;
  top: 0;
  z-index: 10000;
  transition: all .3s ease;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05); 
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 100%;
  max-width: 1500px;
}

.navbar-logo {
  color: rgb(0, 0, 0);
  justify-self: left;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  width: 20%;
}

.nav-menu {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: right;
  width: 70%;
  justify-content: right;
  object-position: right;
  /* margin-right: 30px; */
  position: relative;
}

.nav-item {
  height: 60px;
}

.nav-links {
  color: rgb(0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  height: 100%;
}

.nav-links:hover {
  /* height: 50px; */
  transition: all 0.3s ease-out;
  text-decoration: none;
}


.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.menu-icon img{
  width: 15px;
  height: 15px;
}


.fa-times{
  color: #242222;
  z-index: 1000000000;
}

@media screen and (max-width: 960px) {


  .navbar-logo {
    font-size: 1rem;
    width: 40%;
  }

  .nav-menu {
    color: white;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 40px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    /* justify-content: space-around;
    padding-bottom: 5vh; */
  }

  .nav-menu.active {
    position: fixed;
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1000000000000;
  }

  .nav-links {
    color: white;
    font-weight: bold;
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }


  .menu-icon {
    width:40%;
    display: block;
    text-align: right;
    top: 0;
    font-size: 1.5rem;
    cursor: pointer;
    color:black;
  }


  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    z-index: 100000;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
	font-family: 'Corben', cursive;
	font-size: 2.5rem;
	color: black;
